<script>
import Alert from "./components/Alert";
import Search from "./components/Search";

export default {
    name: "Header",
    components: {
        Alert,
        Search,
    },
    data() {
        return {
            AlertOpen: false,
            SearchOpen: false,
            headerActive: ["JoinStep1", "JoinStep2", "JoinStep3", "JoinComplete", "Authority"],
        };
    },
    methods: {
        AlertOpenFnc: function () {
            this.AlertOpen = !this.AlertOpen;
            this.SearchOpen = false;
        },
        SearchOpenFnc: function () {
            this.SearchOpen = !this.SearchOpen;
            this.AlertOpen = false;
        },
        headerType() {
            switch (this.$route.fullPath) {
                case "/":
                case "/Class":
                case "/not/class": {
                    return {
                        logo: true,
                        alarm: true,
                        search: true,
                        mypage: true,
                    };
                }
                case "/join/step1": {
                    return {
                        back: true,
                        overallProgress: 3,
                        currentProgress: 1,
                    };
                }
                case "/join/step2": {
                    return {
                        back: true,
                        overallProgress: 3,
                        currentProgress: 2,
                    };
                }
                case "/join/step3": {
                    return {
                        back: true,
                        overallProgress: 3,
                        currentProgress: 3,
                    };
                }
                case "/authority":
                case "/place/pay/complete":
                case "/Inquiry/complete":
                case "/Review/Select": {
                    return {
                        close: true,
                    };
                }
                case "/filter": {
                    return {
                        back: true,
                        close: true,
                    };
                }
                case "/Place/Detail": {
                    return {
                        back: true,
                        search: true,
                        mypage: true,
                    };
                }
                case "/Terms":
                case "/Inquiry":
                case "/faq":
                case "/event-news":
                case "/coupon":
                case "/orderinquiry":
                case "/orderinquiryView":
                case "/cancelInquiry":
                case "/mypage":
                case "/MyPage": {
                    return {
                        back: true,
                        home: true,
                    };
                }
                case "/review/detail":
                case "/my-review": {
                    return {
                        back: true,
                        write: true,
                        search: true,
                        mypage: true,
                    };
                }
                case "/wish": {
                    return {
                        search: true,
                        mypage: true,
                    };
                }
                default: {
                    return {
                        back: true,
                    };
                }
            }
        },
        headerTitle() {
            switch (this.$route.fullPath) {
                case "/find/id/step1":
                case "/find/id/step2": {
                    return " ";
                }
                case "/find/pw/step1":
                case "/find/pw/step2": {
                    return " ";
                }
                case "/find/pw/email/step1": {
                    return "  ";
                }
                case "/pw/reset": {
                    return " ";
                }
                case "/join/step1": {
                    return "  ";
                }
                case "/join/step2": {
                    return "";
                }
                case "/join/step3": {
                    return " ";
                }
                case "/filter": {
                    return "";
                }
                case "/place/machine-choice": {
                    return " ";
                }
                case "/place/pay": {
                    return "";
                }
                case "/place/pay/complete": {
                    return " ";
                }
                case "/Place/Review": {
                    return " ";
                }
                case "/Place/Trainer": {
                    return "1:1 ";
                }
                case "/Terms": {
                    return "  ";
                }
                case "/faq": {
                    return "  ";
                }
                case "/coupon": {
                    return "";
                }
                case "/orderinquiry": {
                    return "";
                }
                case "/orderinquiryView":
                case "/OrderInquiryView": {
                    return " ";
                }
                case "/cancelInquiry": {
                    return " ";
                }
                case "/Account/Delete": {
                    return " ";
                }
                case "/MyPage": {
                    return "";
                }
                case "/Review/Select": {
                    return " ";
                }
                case "/interest/region": {
                    return " ";
                }
                case "/Profile_email_login":
                case "/Profile_sns_login": {
                    return "";
                }
                case "/Inquiry":
                case "/Inquiry/complete": {
                    return " ";
                }
                case "/Change/Nickname":
                case "/change/nickname": {
                    return " ";
                }
                case "/Change/Password":
                case "/change/password":
                case "/Change/Password/Confirm":
                case "/change/password/confirm": {
                    return " ";
                }
                default: {
                    return false;
                }
            }
        },
    },
};
</script>

<template>
    <header
        class="at-header"
        :style="{
            backgroundImage: `linear-gradient(90deg,#f1f1f1 ${(headerType()['currentProgress'] / headerType()['overallProgress']) * 100}%, #fff ${
                1 - (headerType()['currentProgress'] / headerType()['overallProgress']) * 100
            }%)`,
        }"
        :class="headerActive.includes($route.name) ? 'active' : ''"
    >
        <div class="at-header-inner">
            <div class="back" v-if="headerType()['back']"><img src="@/assets/image/back.png" alt="" /></div>
            <div class="logo" v-if="headerType()['logo']">
                <img src="@/assets/image/logo.png" alt=" " />
            </div>
            <h1 v-if="headerTitle()" class="f-16-400">{{ headerTitle() }}</h1>
            <div></div>
            <ul class="menu-ul">
                <li v-if="headerType()['alarm']" @click="AlertOpenFnc"><img src="@/assets/image/alarm.png" alt="" /></li>
                <li v-if="headerType()['write']"><img src="@/assets/image/write.png" alt="" /></li>
                <li v-if="headerType()['search']" @click="SearchOpenFnc"><span class="search"></span></li>
                <li
                    v-if="headerType()['mypage']"
                    @click="
                        AlertOpen = false;
                        SearchOpen = false;
                    "
                >
                    <router-link to="/MyPage"><img src="@/assets/image/person.png" alt="" /></router-link>
                </li>
                <li v-if="headerType()['home']">
                    <router-link to="/"><img src="@/assets/image/home.png" alt="" /></router-link>
                </li>
                <li v-if="headerType()['setting']"><img src="@/assets/image/setting.png" alt="" /></li>
                <li v-if="headerType()['close']"><img src="@/assets/image/close.png" alt="" /></li>
                <li v-if="headerType()['overallProgress']" class="progress">{{ headerType()["currentProgress"] }}/{{ headerType()["overallProgress"] }}</li>
            </ul>
        </div>

        <Alert :class="{ active: AlertOpen }" />
        <Search :class="{ active: SearchOpen }" />
    </header>
</template>
